@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
      font-family: "Dana";
      src: local("Dana") , url("../public/fonts/Dana/woff2/DanaFaNum-Regular.woff2") format("woff2");
      font-display: swap;
  }
  @font-face {
      font-family: "Dana Medium";
      src: local("Dana") , url("../public/fonts/Dana/woff2/DanaFaNum-Medium.woff2") format("woff2");
      font-display: swap;
  }
  @font-face {
      font-family: "Dana DemiBold";
      src: local("Dana") , url("../public/fonts/Dana/woff2/DanaFaNum-DemiBold.woff2") format("woff2");
      font-display: swap;
  }
  @font-face {
      font-family: "Morabba Light";
      src: local("Morabba") , url("../public/fonts/Morabba/woff2/Morabba-Light.woff2") format("woff2");
      font-display: swap;
  }
  @font-face {
      font-family: "Morabba Medium";
      src: local("Morabba") , url("../public/fonts/Morabba/woff2/Morabba-Medium.woff2") format("woff2");
      font-display: swap;
  }
  @font-face {
      font-family: "Morabba Bold";
      src: local("Morabba") , url("../public/fonts/Morabba/woff2/Morabba-Bold.woff2") format("woff2");
      font-display: swap;
  }

}

@layer components {
    .modal-parent{
        @apply w-full h-screen fixed top-1/2 -translate-x-1/2 -translate-y-1/2  left-1/2 flex items-center justify-center z-10 bg-black/35 invisible opacity-0;
    }

    .modals-parent{
        @apply w-full h-screen fixed top-1/2 -translate-x-1/2 -translate-y-1/2  left-1/2 flex items-center justify-center z-10 bg-black/35 ;
    }

    .modal-parent--active{
        @apply visible opacity-100;
    }
}